// Site structure

.container {
	max-width: 38rem;
	padding-left: 1.5rem;
	padding-right: 1.5rem;
	margin-left: auto;
	margin-right: auto;
}

footer {
	margin-top: 2rem;
	margin-bottom: 2rem;
	text-align: center;
	font-size: 90%;
}

ul.social {
	list-style-type: none;
	position: relative;
	svg {
		height: 1.5rem;
		width: 1.5rem;
	}
}

ul.social li {
	display: inline-block;
	padding: 10px;
}

main {
	padding: 2rem 0;
	min-height: 60vh;
}
